import { Component, OnInit, Input, effect } from '@angular/core'
import {
  ModalController,
  AlertController,
  LoadingController,
} from '@ionic/angular/standalone'
import { CommonModule } from '@angular/common'
import { IonicModule } from '@ionic/angular'
import { TranslateModule, TranslateService } from '@ngx-translate/core'
import { ForfaitsWrapperService } from 'src/app/services/api-wrappers/forfaits.service'
import {
  AbonnementsState,
  AbonnementWrapperService,
  CreateAbonnementParams,
  CurrentAbonnementState,
} from 'src/app/services/api-wrappers/abonnement.service'
import { ListForfait200ResponseAllOfDataInner } from 'src/api-cyclist'
import { Router } from '@angular/router'
import { FormsModule } from '@angular/forms'
import { SubscriptionProcessService } from 'src/app/services/utils/subscription-process.service'
import { AuthService, AuthState } from 'src/app/services/external/auth.service'
import {
  AccountService,
  cyclistState,
} from 'src/app/services/api-wrappers/account.service'
import {
  AppState,
  AppstateService,
} from 'src/app/services/internal/appstate.service'

type ForfaitsState =
  | {
      state: 'loading'
    }
  | {
      state: 'success'
      forfaits: ListForfait200ResponseAllOfDataInner[]
      selectedForfait?: ListForfait200ResponseAllOfDataInner
    }
  | {
      state: 'error'
      error: Error
    }

@Component({
  selector: 'app-forfaits',
  templateUrl: './forfaits.component.html',
  styleUrls: ['./forfaits.component.scss'],
  imports: [CommonModule, IonicModule, TranslateModule, FormsModule],
})
export class ForfaitListModalComponent implements OnInit {
  @Input() next: 'onboarding' | 'menu' = 'menu'
  aboState: AbonnementsState = { state: 'loading' }
  currentAboState: CurrentAbonnementState = { state: 'unknown' }
  forfaitsState: ForfaitsState = { state: 'loading' }
  authState: AuthState = { state: 'SignedOut' }
  cyclistState: cyclistState = {
    state: 'unknown',
    blocked_reasons: ['Unknown account state'],
  }
  promoCode: string = ''
  reduction: string = ''
  constructor(
    public modalCtrl: ModalController,
    public forfaitsWrapperService: ForfaitsWrapperService,
    public AbonnementWrapperService: AbonnementWrapperService,
    private alertController: AlertController,
    private router: Router,
    private translate: TranslateService,
    private loadingCtrl: LoadingController,
    private subscriptionProcess: SubscriptionProcessService,
    private authService: AuthService,
    public accountService: AccountService
  ) {
    effect(() => {
      this.aboState = this.AbonnementWrapperService.listAbonnementsState()
    })
    effect(() => {
      this.currentAboState =
        this.AbonnementWrapperService.currentAbonnementState()
    })
    effect(() => {
      this.authState = this.authService.authState()
    })
    effect(() => {
      this.cyclistState = this.accountService.cyclistState()
    })
  }

  ngOnInit() {
    this.forfaitsWrapperService.getForfaits().then((forfaits) => {
      if (forfaits) {
        this.forfaitsState = { state: 'success', forfaits }
      } else {
        this.forfaitsState = {
          state: 'error',
          error: new Error('Error loading forfaits'),
        }
      }
    })
  }
  async handleForfaitSelection() {
    if (
      this.forfaitsState.state === 'success' &&
      this.forfaitsState.selectedForfait &&
      this.forfaitsState.selectedForfait.id
    ) {
      if (this.next === 'onboarding') {
        await this.subscriptionProcess.startProcess(
          this.forfaitsState.selectedForfait,
          this.reduction
        )
        this.modalCtrl.dismiss({
          forfait: this.forfaitsState.selectedForfait,
          reduction: this.reduction,
        })
      } else {
        const alert = await this.alertController.create({
          header: "Confirmer l'abonnement",
          message: 'Voulez-vous vraiment souscrire Ã  ce forfait ?',
          buttons: [
            {
              text: 'Annuler',
              role: 'cancel',
            },
            {
              text: 'Confirmer',
              handler: async () => {
                if (
                  this.forfaitsState.state === 'success' &&
                  this.forfaitsState.selectedForfait
                ) {
                  await this.subscriptionProcess.startProcess(
                    this.forfaitsState.selectedForfait,
                    this.reduction
                  )

                  if (
                    this.forfaitsState.selectedForfait.supporting_documents &&
                    this.forfaitsState.selectedForfait.supporting_documents
                      ?.length > 0
                  ) {
                    await this.modalCtrl.dismiss()
                    await this.router.navigate(['/supporting-documents'])
                  } else {
                    const payload =
                      await this.subscriptionProcess.getCreateAbonnementPayload()
                    if (payload) {
                      this.createAbonnement(payload)
                      await this.subscriptionProcess.clearProcess()
                    } else {
                      throw new Error('No subscription process found')
                    }
                  }
                }
              },
            },
          ],
        })
        await alert.present()
      }
    }
  }

  async createAbonnement(payload: CreateAbonnementParams) {
    const loading = await this.loadingCtrl.create({
      message: this.translate.instant('subscription.creation.loading'),
    })
    await loading.present()

    this.AbonnementWrapperService.createAbonnement(payload).finally(
      async () => {
        await loading.dismiss()
        await this.modalCtrl.dismiss()
      }
    )
  }

  selectForfait(forfait: ListForfait200ResponseAllOfDataInner) {
    if (this.forfaitsState.state === 'success') {
      this.forfaitsState.selectedForfait = forfait
    }
  }
}
